@import '../mixins/mixins.module.scss';

.profile {
    @include main;

    .form {
        border: 1px solid #F3F3F3;
        border-radius: 8px;
        padding: 20px 12px;
        margin-top: 16px;

        .name {
            font-size: 8px;
        }

        input {
            border: 1px solid #D8D8D8;
            border-radius: 5px;
            padding: 5px 10px;
            margin-top: 3px;
            outline: none;
        }
    }
}
