.user {
    width: 420px;
    background: #F3F3F3;
    border-radius: 8px;
    padding: 10px;
    margin-top: 15px;
    display: flex;
    justify-content: space-between;

    .fields {
        font-size: 11px;

        .field {
            margin-top: 8px;

            .name {
                color: #A3A3A3;
                margin-right: 7px;
            }
        }
    }

    .more {
        align-self: flex-end;
        color: #4B51EF;
        font-size: 12px;
        cursor: pointer;
    }
}







